*{
  font-family: 'Josefin Sans', sans-serif;
}

.main_div{
  width: 100%;
  height: 100vh;
  background-color:lightblue;
  justify-content: center;
  display: flex;
  text-align: center;
  align-items: center;
  /* flex-direction: row; */
}
.center_div{
  width: 25%;
  height: 80vh;
   background-color: white;
   box-shadow: 5px 5px 25px 5px rgba(0,0,0,0.5);
   border-radius: 20px;
}
h1{
  color: white;
  /* background: transparent; */
  background-color: purple;
  padding: 6px 0 2px 0;
  box-shadow: 5px 5px 25px 5px rgba(0,0,0,0.5);
}
input{
  text-align: center;
  height: 30px;
  width: 60%;
  font-size: 20px;
  border-bottom: 2px solid purple;
  font-size: 500;
  border-radius: 10px;
  border:none;
  outline:none;
}
button{
  border-radius: 50px;
  min-height: 40px;
  width:40px ;
  background-color: violet;
  box-shadow: 5px 5px 25px 5px rgba(0,0,0,0.5);
  border:none;
  outline:none;
  color:white;
  font-size: 20px;
}
 
button:hover{
  background-color: green;
  cursor: pointer;
}

ol{
  margin-top:30px;
}
 ol li{
   text-align: left;
   text-transform: capitalize;
   font-size: 25px;
   color: violet;
   align-items: center;
   display: flex;
}
.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}
.todo_style .fa-times{
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: aqua;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  color:blue;
}

.todo_style .fa-times:hover{
  background-color: crimson;
}
hr{
  width: 50%;
}

